.Lectorem {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 5px;
  padding: 5px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

audio {
  filter: invert(20%);
  width: 400px;
  height: 50px;
}

.Lectorem .playerContainer {
  padding: 5px;
}

.Lectorem .paragraph {
  text-align: left;
  margin: 5px;
  padding: 5px;
}

.Lectorem h2 {
  margin: 5px;
  padding: 0;
}

.Lectorem h3 {
  margin: 5px;
  padding: 0;
}